body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Roboto, serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1, h2, h3, h4{
  margin: 0 0;
}

a{
  text-decoration: none;
  color: inherit;
}

a:active{
  color: inherit;
}
.sidedrawer__store-link{
    text-decoration: none;
}
#logo{
    cursor: pointer;
}
#navBar{
    width: 100%;
}

.navBar_toolbar {
    background-color: #581002;
    color: #F7F6F1;
}

.navigation__link{
    color: #F7F6F1;
    -webkit-transition: color 0.5s;
    transition: color 0.5s;
    padding: 0 0.5rem;
    cursor: pointer;
}

.navigation__link:hover,
.navigation__link:active{
    color: blue;
}

#home_content{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    padding-bottom: 75px;
}

.home__text{
    margin-top: -150px;
    color: #581002;
}

#home_content h1{
    padding: 1px 1px;
}

#home_content .section-1__button{
    margin: 10px 100px;
}
#about_us{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 75px 15px;
    line-height:1.3rem;
  }
  .about_us__photo1{
    max-width: 100%;
    max-height: 100%;
    border-radius: 7px;
  }
  .about_us__title{
    text-align: center;
    padding-right: 15px;
    margin-bottom: 25px;
  }
  .about_us__content{
    text-align: right;
    padding-right: 15px;
    margin-bottom: 25px;
  }
  #about_us section {
    max-width: 1200px;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
  }
  
  .column {
    margin: 10px;
    -webkit-flex-grow: 2;
            flex-grow: 2;
    -webkit-flex-shrink: 2;
            flex-shrink: 2;
    -webkit-flex-basis: 0;
            flex-basis: 0;
    text-align: center;
  }

  .flex {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  p{
    text-align: justify;
  }
  .social{
    display: -webkit-inline-flex;
    display: inline-flex;
  }

